<template lang="html">
  <div>
    <Header></Header>
    <vs-row vs-justify="center">
      <vs-col vs-w="11">
        <vs-card>
          <div>
            <network
              ref="network"
              :nodes="nodes"
              :edges="edges"
              :options="options"
            >
            </network>
          </div>
          <vs-row>
            <vs-col vs-lg="2" vs-xs="12" vs-sm="12" vs-justify="space-around">
              <div class="pt-5">
                <h3 class="pl-2 titulos">Individuos Aleatorios</h3>
              </div>
              <ValidationObserver v-slot="{ invalid }">
                <ValidationProvider
                  name="No. Individuos"
                  :rules="{ required: true, numeric: true, min: 1, max: 5 }"
                  v-slot="{ errors }"
                >
                  <vs-input
                    style="width:100%;"
                    icon-after="true"
                    icon="format_align_justify"
                    label="Ingresa el número de indiviuos"
                    placeholder=""
                    v-model="noIndividuos"
                    @keyup.enter="
                      invalid
                        ? !invalid
                        : aleaIndividuos(
                            noIndividuos,
                            tamanoIndividuos,
                            xMin,
                            xMax
                          )
                    "
                  />
                  <span style="color: red;">{{ errors[0] }}</span>
                </ValidationProvider>
                <ValidationProvider
                  name="Longitud de Individuos"
                  :rules="{ required: true, numeric: true, min: 1, max: 5 }"
                  v-slot="{ errors }"
                >
                  <vs-input
                    icon-after="true"
                    style="width:100%;"
                    icon="accessibility"
                    label="Longitud de indiviuos"
                    placeholder=""
                    v-model="tamanoIndividuos"
                    @keyup.enter="
                      invalid
                        ? !invalid
                        : aleaIndividuos(
                            noIndividuos,
                            tamanoIndividuos,
                            xMin,
                            xMax
                          )
                    "
                  />
                  <span style="color: red;">{{ errors[0] }}</span>
                </ValidationProvider>
                <div class="pt-2">
                  <h3 class="pl-2 titulos">Rangos</h3>
                </div>
                <ValidationProvider
                  name="Valor maximo"
                  :rules="{ required: true, numeric: true, min: 1, max: 5 }"
                  v-slot="{ errors }"
                >
                  <vs-input
                    icon-after="true"
                    style="width:100%;"
                    icon="expand_less"
                    label="Valor maximo"
                    placeholder=""
                    v-model="xMax"
                    @keyup.enter="
                      invalid
                        ? !invalid
                        : aleaIndividuos(
                            noIndividuos,
                            tamanoIndividuos,
                            xMin,
                            xMax
                          )
                    "
                  />
                  <span style="color: red;">{{ errors[0] }}</span>
                </ValidationProvider>
                <ValidationProvider
                  name="Valor mínimo"
                  :rules="{ required: true, numeric: true, min: 1, max: 5 }"
                  v-slot="{ errors }"
                >
                  <vs-input
                    icon-after="true"
                    style="width:100%;"
                    icon="expand_more"
                    label="Valor mínimo"
                    placeholder=""
                    v-model="xMin"
                    @keyup.enter="
                      invalid
                        ? !invalid
                        : aleaIndividuos(
                            noIndividuos,
                            tamanoIndividuos,
                            xMin,
                            xMax
                          )
                    "
                  />
                  <span style="color: red;">{{ errors[0] }}</span>
                </ValidationProvider>
                <div class="pt-2 pl-1">
                  <vs-button
                    color="success"
                    type="relief"
                    :disabled="invalid"
                    @click="
                      aleaIndividuos(noIndividuos, tamanoIndividuos, xMin, xMax)
                    "
                    >Agregar</vs-button
                  >
                </div>
              </ValidationObserver>
            </vs-col>
            <vs-col class="pl-5" vs-lg="10" vs-xs="12" vs-sm="12">
              <vs-table
                stripe
                search
                max-items="5"
                pagination
                :data="individuos"
              >
                <template slot="header">
                  <h3 class="text-3xl">
                    Individuos Aleatorios
                  </h3>
                </template>
                <template slot="thead">
                  <vs-th class="text-2xl">
                    No. Individuo
                  </vs-th>
                  <vs-th class="text-2xl">
                    Individuo
                  </vs-th>
                  <vs-th class="text-2xl">
                    Valor Decimal
                  </vs-th>
                  <vs-th class="text-2xl">
                    Valor Real
                  </vs-th>
                  <vs-th class="text-2xl">
                    Valor Adaptado
                  </vs-th>
                </template>

                <template slot-scope="{ data }">
                  <vs-tr :key="indextr" v-for="(tr, indextr) in data">
                    <vs-td :data="data[indextr].id">
                      {{ data[indextr].id }}
                    </vs-td>

                    <vs-td :data="data[indextr].individuo">
                      {{ data[indextr].individuo }}
                    </vs-td>

                    <vs-td :data="data[indextr].valorDecimal">
                      {{ data[indextr].valorDecimal }}
                    </vs-td>

                    <vs-td :data="data[indextr].valorReal">
                      {{ data[indextr].valorReal }}
                    </vs-td>

                    <vs-td :data="data[indextr].valorAdaptado">
                      {{ data[indextr].valorAdaptado }}
                    </vs-td>
                  </vs-tr>
                </template>
              </vs-table>
            </vs-col>
          </vs-row>
        </vs-card>
      </vs-col>
    </vs-row>
    <vs-row vs-justify="center">
      <vs-col vs-w="11">
        <vs-card>
          <vs-row>
            <vs-col vs-lg="12" vs-xs="12" vs-sm="12">
              <vs-table
                stripe
                search
                max-items="5"
                pagination
                :data="individuos"
              >
                <template slot="header">
                  <h3 class="text-3xl">
                    Alcaldias
                  </h3>
                </template>
                <template slot="thead">
                  <vs-th class="text-2xl">
                    Iztapalapa
                  </vs-th>
                  <vs-th class="text-2xl">
                    Gustavo_A_Madero
                  </vs-th>
                  <vs-th class="text-2xl">
                    Tlapan
                  </vs-th>
                  <vs-th class="text-2xl">
                    Miguel_Hidalgo
                  </vs-th>
                </template>

                <template slot-scope="{ data }">
                  <vs-tr :key="indextr" v-for="(tr, indextr) in data">
                    <vs-td :data="data[indextr].id">
                      {{ data[indextr].id }}
                    </vs-td>

                    <vs-td :data="data[indextr].individuo">
                      {{ data[indextr].individuo }}
                    </vs-td>

                    <vs-td :data="data[indextr].valorDecimal">
                      {{ data[indextr].valorDecimal }}
                    </vs-td>

                    <vs-td :data="data[indextr].valorReal">
                      {{ data[indextr].valorReal }}
                    </vs-td>
                  </vs-tr>
                </template>
              </vs-table>
            </vs-col>
          </vs-row>
        </vs-card>
      </vs-col>
    </vs-row>

    <vs-row vs-justify="center">
      <vs-col vs-w="11">
        <vs-card>
          <vs-alert title="INFORMACIÓN" active="true">
            Para calcular la ruta optima en el mapa, mueva los iconos de
            posicion que están en la pantalla.
          </vs-alert>
          <vs-row vs-justify="center">
            <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="4">
              <vs-card actionable class="cardx">
                <div slot="header">
                  <h3>
                    Punto de origen
                  </h3>
                </div>
                <div slot="media">
                  <img
                    src="static/images/download.png"
                    style="width: 11%;margin-left: auto;margin-right: auto;"
                  />
                </div>
                <div>
                  <span
                    >El icono ubicado del lado izquierdo del mapa indica ser el
                    punto de origen</span
                  >
                </div>
              </vs-card>
            </vs-col>
            <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="4">
              <vs-card actionable class="cardx">
                <div slot="header">
                  <h3>
                    Encuentra tu sector
                  </h3>
                </div>
                <div slot="media">
                  <img
                    src="static/images/cuadrante1.png"
                    style="width: 18%;margin-left: auto;margin-right: auto;"
                  />
                </div>
                <div>
                  <span
                    >Mueva el icono para encotrar el sector en el que te
                    encuentras</span
                  >
                </div>
              </vs-card>
            </vs-col>
            <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="4">
              <vs-card actionable class="cardx">
                <div slot="header">
                  <h3>
                    Punto de destino
                  </h3>
                </div>
                <div slot="media">
                  <img
                    src="static/images/download.png"
                    style="width: 11%;margin-left: auto;margin-right: auto;"
                  />
                </div>
                <div>
                  <span
                    >El icono de posicion ubicado del lado derecho en el mapa,
                    indica ser el punto de destino</span
                  >
                </div>
              </vs-card>
            </vs-col>
          </vs-row>
          <br />
          <mapa />
        </vs-card>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
import Header from '@/pages/Header.vue'
import mapa from '@/pages/Mapa.vue'
import VueSimpleSuggest from 'vue-simple-suggest'
import 'vue-simple-suggest/dist/styles.css'
import ServicesGeneticos from '@/Services/ServicesGeneticos'
import ServicesDijkstra from '@/Services/ServicesDijkstra'
import Alcaldias from '../../static/Alcaldias/alcaldias.json'
import Incidencias from '../../static/Alcaldias/incidencias.json'
import Vue from 'vue'
import { Network } from 'vue-vis-network'
Vue.component('network', Network)

export default {
  components: {
    Header,
    mapa,
    VueSimpleSuggest
  },
  data () {
    return {
      individuos: [],
      noIndividuos: null,
      tamanoIndividuos: null,
      xMax: null,
      xMin: null,
      checkBox1: null,
      popupActivo5: true,
      colorx: '#0a540a',
      totalMatriz: null,
      nodes: [
        { id: 1, label: 'circle', shape: 'circle' },
        { id: 2, label: 'ellipse', shape: 'circle' },
        { id: 3, label: 'database', shape: 'circle' },
        { id: 4, label: 'box', shape: 'circle' },
        { id: 5, label: 'diamond', shape: 'circle' },
        { id: 6, label: 'dot', shape: 'circle' },
        { id: 7, label: 'square', shape: 'circle' },
        { id: 8, label: 'triangle', shape: 'circle' }
      ],
      edges: [
        { from: 1, to: 2 },
        { from: 1, to: 1 },
        { from: 2, to: 3 },
        { from: 2, to: 4 },
        { from: 2, to: 5 },
        { from: 5, to: 6 },
        { from: 5, to: 7 },
        { from: 6, to: 8 }
      ],
      options: {
        height: '100%',
        width: '100%',
        nodes: {
          borderWidth: 4
        },
        edges: {
          color: 'lightgray',
          arrows: 'from',
          label: 'Hola mudno'
        }
      }
    }
  },
  methods: {
    aleaIndividuos (long, tamano, xmin, xmax) {
      let vm = this
      vm.individuos = []
      for (let i = 0; i < long; i++) {
        let individuo = ServicesGeneticos.individuosAleatorios(tamano)
        let valorDecimal = ServicesGeneticos.binarioDecimal(individuo)
        let valorReal = ServicesGeneticos.valorReal(
          xmin,
          xmax,
          valorDecimal,
          long
        )
        let valorAdaptado = ServicesGeneticos.valorAdaptado(
          valorReal,
          vm.totalMatriz
        )

        vm.individuos.push({
          id: i + 1,
          individuo: individuo,
          valorDecimal: valorDecimal,
          valorReal: valorReal,
          valorAdaptado: valorAdaptado
        })
      }
    }
  },

  mounted () {
    let nameAlcaldias = []

    for (let i in Alcaldias) {
      nameAlcaldias.push(i)
    }
    nameAlcaldias.forEach(e => console.log(e))
    // console.log(ServicesDijkstra.permutator(nameAlcaldias))

    var array = nameAlcaldias
    var results = []
    // Since you only want pairs, there's no reason
    // to iterate over the last element directly
    for (var i = 0; i < array.length - 1; i++) {
      // This is where you'll capture that last value
      for (var j = i + 1; j < array.length; j++) {
        results.push([array[i], array[j]])
      }
    }

    let resultados = []
    results.forEach(elementos => {
      resultados.push(
        ServicesDijkstra.distanciaManhattan(
          Alcaldias[elementos[0]],
          Alcaldias[elementos[1]]
        )
      )
    })
    let matriz = []
    let suma = 0
    let jerarquizacionAnalitica = []
    matriz.push([0, resultados[0], resultados[1], resultados[2]])
    matriz.push([resultados[0], 0, resultados[3], resultados[4]])
    matriz.push([resultados[1], resultados[3], 0, resultados[5]])
    matriz.push([resultados[2], resultados[4], resultados[5], 0])
    matriz.forEach(e => console.log(typeof parseFloat(e[0])))
    matriz.forEach(fila => {
      fila.forEach(columna => {
        suma = suma + columna
      })
      jerarquizacionAnalitica.push(suma / fila.length)
    })

    // ========================= incidencias =========================
    let nameIncidencias = []
    for (i in Incidencias[nameAlcaldias[0]]) nameIncidencias.push(i)

    let totales = {}
    for (let i in Incidencias) {
      let suma = 0
      let long = 0
      for (let j in Incidencias[i]) {
        suma = suma + Incidencias[i][j]
        long = long + 1
      }

      totales[i] = suma
      long = 0
    }

    let resulIncidencias = []
    results.forEach(elementos => {
      resulIncidencias.push(totales[elementos[0]] + totales[elementos[1]])
    })

    let matrizI = []
    let sumaI = 0
    let jerarquizacionAnaliticaI = []
    matrizI.push([
      0,
      resulIncidencias[0],
      resulIncidencias[1],
      resulIncidencias[2]
    ])
    matrizI.push([
      resulIncidencias[0],
      0,
      resulIncidencias[3],
      resulIncidencias[4]
    ])
    matrizI.push([
      resulIncidencias[1],
      resulIncidencias[3],
      0,
      resulIncidencias[5]
    ])
    matrizI.push([
      resulIncidencias[2],
      resulIncidencias[4],
      resulIncidencias[5],
      0
    ])
    matrizI.forEach(e => console.log(typeof parseFloat(e[0])))
    matrizI.forEach(fila => {
      fila.forEach(columna => {
        sumaI = sumaI + columna
      })
      jerarquizacionAnaliticaI.push(suma / fila.length)
    })
    console.log(jerarquizacionAnaliticaI)

    let totalAlcaldias = 0
    let totalIncidencias = 0
    jerarquizacionAnaliticaI.forEach(elemento => {
      totalAlcaldias = totalAlcaldias + elemento
    })

    jerarquizacionAnalitica.forEach(elemento => {
      totalIncidencias = totalIncidencias + elemento
    })

    console.log('total de alcaldias: ' + totalAlcaldias)
    console.log('total de incidencias: ' + totalIncidencias)

    console.log('F(x) = ' + totalIncidencias / totalAlcaldias)
    this.totalMatriz = totalIncidencias / totalAlcaldias
    // let a = ServicesDijkstra.distanciaManhattan(
    //   Alcaldias.Alcaldia_Gustavo_A_Madero,
    //   Alcaldias.Alcaldia_Iztapalapa
    // )

    // let a = ServicesDijkstra.distanciaManhattan([6, 22], [-26, -47])
    // let vm = this
    // let ejemplo = [
    //   { index: 0, parent: -1 },
    //   { index: 2, parent: [{ index: 0, value: 10, markerStart: 'arrow' }] },
    //   { index: 1, parent: [{ index: 0, value: 50, markerStart: 'arrow' }] },
    //   {
    //     index: 3,
    //     parent: [
    //       { index: 2, value: 10, markerStart: 'arrow', lineTextOffset: -1 },
    //       { index: 4, value: 50, markerEnd: 'arrow' },
    //       { index: 0, value: 100, markerStart: 'arrow', lineOffset: -1.5 },
    //       { index: 1, value: 20, markerEnd: 'arrow' }
    //     ]
    //   },
    //   {
    //     index: 4,
    //     parent: [
    //       { index: 1, value: 5, markerEnd: 'arrow', lineTextOffset: 1 },
    //       { index: 0, value: 30, markerStart: 'arrow', lineOffset: 1.5 }
    //     ]
    //   }
    // ]
    // console.log(vm.dijkstra(ejemplo))
  }
}
</script>
<style>
.scroll {
  overflow-x: auto;
}
.titulos {
  color: #0a540a;
  font-size: 19px;
}
</style>



