// Generar cadena de bits
let individuosAleatorios = length => {
  let result = ''
  let characters = '10'
  let charactersLength = characters.length
  for (let i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength))
  }
  return result
}

let binarioDecimal = numeroBinario => {
  let decimal = 0
  let index = 0
  for (let exp = numeroBinario.length - 1; exp >= 0; exp--) {
    let numero = parseInt(numeroBinario[index])
    numero = numero * Math.pow(2, exp)
    decimal = decimal + numero
    index += 1
  }
  return decimal
}

let valorReal = (xMin, xMax, valorDecimal, longitudIndividuo) => {
  return (
    xMin + valorDecimal * ((xMax - xMin) / (Math.pow(2, longitudIndividuo) - 1))
  )
}

let valorAdaptado = (valorReal, totalMatriz) => {
  return valorReal / totalMatriz
}

let funcionAdaptacion = () => {
  return null
}

export default {
  individuosAleatorios,
  binarioDecimal,
  valorReal,
  funcionAdaptacion,
  valorAdaptado
}



