<template>
  <div>
    <l-map ref="myMap" style="height: 500px" :zoom="zoom" :center="center">
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <l-routing-machine :waypoints="waypoints" />

      <l-marker
        :id="origen.id"
        :visible="origen.visible"
        :draggable="origen.draggable"
        :lat-lng.sync="origen.position"
      >
        <l-icon icon-url="static/images/cuadrante1.png"></l-icon>
      </l-marker>

      <l-polygon
        :lat-lngs="polygon.latlngs"
        color="#ffa328"
        fillColor="#ffa328"
      ></l-polygon>
      <l-circle
        v-for="point in crimenes"
        :key="point.id"
        :lat-lng="point.center"
        :color="point.color"
        fillColor="red"
        :fillOpacity="1"
      />
    </l-map>
  </div>
</template>

<script>
import 'leaflet/dist/leaflet.css'
import { Icon } from 'leaflet'
// import L from 'leaflet'
import {
  LMap,
  LTileLayer,
  LMarker,
  LPolygon,
  LCircle,
  LPolyline,
  LIcon
} from 'vue2-leaflet'
import axios from 'axios'
import Crimenes from '@/Services/Crimenes'
import LRoutingMachine from './LRoutingMachine.vue'
delete Icon.Default.prototype._getIconUrl
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
})
require('leaflet-routing-machine')
export default {
  components: {
    LMap,
    LTileLayer,
    LPolygon,
    LMarker,
    LCircle,
    LPolyline,
    LRoutingMachine,
    LIcon
  },
  watch: {
    'origen.position': function (newVal, oldValue) {
      let vm = this
      if (newVal !== oldValue) {
        vm.getGeoJson(vm.origen.position.lat, vm.origen.position.lng)
      }
    },
    'destino.position': function (newVal, oldValue) {
      let vm = this
      if (newVal !== oldValue) {
        vm.getGeoJson(vm.destino.position.lat, vm.destino.position.lng)
      }
    }
  },
  mounted () {
    let vm = this
    vm.mapa()
    vm.rutaCalcular(vm.origen, vm.destino)
    let id = 0
    Crimenes.quinietosMetros().forEach(element => {
      let a = []
      a.push(element.lat)
      a.push(element.long)
      vm.crimenes.push({
        center: a,
        color: 'red',
        id: id
      })
      id++
    })
  },
  data () {
    return {
      waypoints: [
        { lat: 19.42847, lng: -99.12766 },
        { lat: 19.428371, lng: -99.12539 }
      ],
      valorMapa: '',
      style: 'normal.day.transit',
      apiKey: 'IU4thQfzo5Cd6g4e0E0EPIbcrkyP2J5CSbsg0_pyrVo',
      attribution:
        '&copy; <a href="http://developer.here.com">HERE</a> Technologies</a>',
      url: '',
      geojson: null,
      zoom: 16,
      // center: [19.4357619, -99.1441192],
      center: [19.42847, -99.12766],
      markerLatLng: [19.42847, -99.12766],
      a: null,
      origen: {
        id: 'm1',
        position: { lat: 19.42847, lng: -99.12766 },
        tooltip: 'tooltop for marker1',
        draggable: true,
        visible: true
      },
      destino: {
        id: 'm2',
        position: { lat: 19.428371, lng: -99.12539 },
        tooltip: 'tooltop for marker2',
        draggable: true,
        visible: true
      },
      polygon: {
        latlngs: [],
        colors: 'green'
      },
      circle: {
        center: [19.4269968208431, -99.1301963359056],
        color: 'red'
      },
      crimenes: [],

      polyline: {
        latlngs: [],
        color: 'green'
      }
    }
  },
  methods: {
    rutaCalcular (origen, destino) {
      let vm = this

      let config = {
        url: `https://router.hereapi.com/v8/routes`,
        params: {
          transportMode: 'car',
          origin: `${origen.position.lat},${origen.position.lng}`,
          destination: `${destino.position.lat},${destino.position.lng}`,
          apiKey: this.apiKey
        },
        method: 'GET'
      }
      axios(config)
        .then(Response => {
          console.log(Response.data.routes[0].sections[0])
          let a = Response.data.routes[0].sections[0]
          for (let property in a) {
            if (property === 'arrival') {
              console.log(a[property].place.location)
              let latLon = []
              latLon.push(a[property].place.location.lat)
              latLon.push(a[property].place.location.lng)
              vm.polyline.latlngs.push(latLon)
            } else if (property === 'departure') {
              console.log(a[property].place.location)
              let latLon = []
              latLon.push(a[property].place.location.lat)
              latLon.push(a[property].place.location.lng)
              vm.polyline.latlngs.push(latLon)
            }
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    getGeoJson (lat, lng) {
      let vm = this
      vm.$vs.loading({ text: 'Buscando dirección' })

      let config = {
        url: 'http://cuadrantes.ssc.cdmx.gob.mx/api/v1/getcuadrante',
        params: { lat: `${lat}`, lng: `${lng}` },
        method: 'GET',
        headers: {
          'Acces-Control-Allow-Origin': '*'
        }
      }
      axios(config)
        .then(Response => {
          vm.polygon.latlngs = Response.data.data[0].coordenadas
          vm.polygon.latlngs = vm.polygon.latlngs.map(element => {
            let array = []
            element.forEach(valor => {
              array.push(parseFloat(valor))
            })
            return array
          })
          vm.$vs.loading.close()
        })
        .catch(error => {
          vm.$vs.loading.close()
          console.log(error)
        })
    },
    coordenadas (a) {
      console.log(a)
    },

    mapa () {
      let vm = this
      vm.url = `https://2.base.maps.ls.hereapi.com/maptile/2.1/maptile/newest/${this.style}/{z}/{x}/{y}/256/png8?apiKey=${this.apiKey}`
    }
  }
}
</script>

<style></style>



